.styling{
    position: fixed;
    left: 0;
    z-index: 1;
    padding: 0px 0px;
    width: 15%;
}
.bg-dark-l{
    background-color: #313a46;
}
.bg-dark-linear{
    /* background-color: #343a40; */
    /* background: linear-gradient(#343a40,white, 0.75); */
    background: linear-gradient(135deg, #313a46 75%, white);
}
.maining{
    margin-left: auto!important;
}

.fa-file-export{
    font-size: 1.4em;
}
.fa-file-export:hover{
    color: orange;
}
.tbls{
    height: 500px;
    overflow: auto;
}
.thead-head{
    border: solid 1px gray;
}
.thead-tbody{
    overflow: hidden;
    border: solid 1px gray;
}
.thead-tbody tr{
    cursor: pointer;
}
.thead-tbody tr:hover{
    background-color: black;
    color: white;
}
.thead-tbody td{
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 150px;
}
.ipSearch{
    color: white;
    border: none;
    border-radius: 3px;
}
.ipSearch:hover{
    background-color: gray;
}
.ipSearch::placeholder{
    color: white;
    font-weight: 500;
}

.toasts{
    top: 100px;
    left: 250px;
    min-width: 500px;
    overflow: hidden;
    display: none;
    font-size: .875rem;
    background-color: rgba(255,255,255,.75);
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.1);
    box-shadow: 0 0.25rem 0.75rem rgba(0,0,0,.1);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border-radius: .25rem;
    position: fixed;
    z-index: 2;
}
.toasts-header{
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding: .25rem .75rem;
    color: #6c757d;
    background-color: rgba(255,255,255,.85);
    background-clip: padding-box;
    border-bottom: 1px solid rgba(0,0,0,.05);
}
.toasts-body{
    padding: .75rem;
}
.noneMargin{
    margin-bottom: 5px;
}