.styling{
    position: fixed;
    left: 0;
    z-index: 1;
    padding: 0px 0px;
    width: 15%;
}

.maining{
    padding-top: 10px;
    padding-left: 10px;
}

.mini-message{
    width: 30vw;
    height: 90vh;
}
.peer-mini-message{
    cursor: pointer;
    background-color: #ffffff;
    font-size: 1.1em;
    padding: 10px 5px;
    width: 100%;
    height: 100px;
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,.1);
    box-shadow: 0 0.25rem 0.75rem rgba(0,0,0,.1);
}
.peer-mini-message:hover{
    background-color: white;
    box-shadow: 0.25rem 0.75rem 1.5rem rgba(0,0,0,.1);
}
.peer-mini-cusname{
    margin-left: 10px;
    font-weight: 500;
    max-height: 50%;
    overflow: hidden;
}
.peer-mini-cusmail{
    max-width: 50%;
    overflow: hidden;
    text-overflow: ellipsis;
}
.peer-mini-mesdescrp{
    padding: 10px 20px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    color: gray;
    white-space: nowrap;
}
.response-message{
    margin: 20px 20px;
    border-radius: 10px;
}
.jumbotrons{
    padding: 4rem 2rem;
    margin: 2rem 2rem;
    margin-bottom: 2rem;
    background-color: #e9ecef;
    border-radius: .3rem;
    border: solid 1px lightgray;
}
.toastss.show{
    display: inline-block;
}
.toastss{
    bottom: 20px;
    right: 20px;
    min-width: 500px;
    overflow: hidden;
    display: none;
    font-size: .875rem;
    background-color: rgba(255,255,255,.75);
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.1);
    box-shadow: 0 0.25rem 0.75rem rgba(0,0,0,.1);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border-radius: .25rem;
    position: fixed;
    z-index: 2;
    animation: showing 0.3s linear;
}

@keyframes showing{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}