.pop-up{
  position: absolute;
  z-index: 1;
}
.pop-up .pop-up-main{
  z-index: 3;
  position: relative;
  width: 100vw;
}
.pop-up .background-popup{
  width: 100vw;
  min-height: 100vh;
  height: auto;
  position: absolute;
  z-index: 1;
  background-color: gray;
  animation: visible 0.5s;
  opacity: 0.5;
}
@keyframes unvisible {
  from{
      opacity: 1;
  }
  to{
      opacity: 0;
  }
}
